import api from '../../api.js'
const state = {
    produtosType:[],
    produto:[],
    todosProdutos:[]    
}
const getters={
    getProdutosType:state=>state.produtosType,
    getProduto:state=>state.produto,
    getTodosProdutos:state=>state.todosProdutos
}
const mutations={
    insertProdutosType:(state,produtosType)=>(state.produtosType = produtosType),
    insertProduto:(state,produto)=>(state.produto = produto),
    insertTodosProdutos:(state,todosProdutos)=>(state.todosProdutos = todosProdutos)
}
const actions ={ 
    fetchProdutosType({commit},type){
        api(`http://localhost:4000/produto/type/${type}`,'GET')
        .then((resp)=>resp.json())
        .then((resp)=>{
            commit('insertProdutosType',resp)
        })  
    },
    fetchTodosProdutos({commit}){
        api('http://localhost:4000/produto','GET')
        .then((resp)=>resp.json())
        .then((resp)=>{
            commit('insertTodosProdutos',resp)
        })
    },
    fetchOneProduto({commit}){
        api('','','')
        .then((resp)=>resp.json())
        .then((resp)=>{
            commit('insertProduto',resp)
        })  
    },
    
}


export default {
    state,
    mutations,
    actions,
    getters
}