import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue'),
    
  },
  {
    path: '/kimono',
    name: 'kimono',
    component: () => import('../views/Kimono.vue'),
    children: [
      {
        path: 'karate/:type',
        name:'karate',
        component:()=>import('../components/produtos/karate.vue'),
        props: true
      },
      {
        path: 'karate-infantil/:type',
        name:'karate-infantil',
        component:()=>import('../components/produtos/karateInfantil.vue'),
        props: true
      },
      {
        path: 'aikido/:type',
        name:'aikido',
        component:()=>import('../components/produtos/aikido.vue'),
        props: true
      },
      {
        path: 'aikido-infantil/:type',
        name:'aikido-infantil',
        component:()=>import('../components/produtos/aikidoInfantil.vue'),
        props: true
      },
      {
        path: 'faixa/:type',
        name:'faixa',
        component:()=>import('../components/produtos/faixa.vue'),
        props: true
      },
      {
        path: ':id',
        name: 'onekimono',
        component: () => import('../components/produtos/oneKimono.vue'),
        props: true
      },
    ]
  },
  {
    path: '/',
    component: () => import('../views/Form.vue'),
    children: [
      {
        path: 'cadastro',
        name:'cadastro',
        component:()=>import('../components/forms/cadastro.vue')
        
      },
      {
        path: 'login',
        name:'login',
        component:()=>import('../components/forms/login.vue')
        
      }
    ]
  },
  {
    path: '/car',
    name:'car',
    component: () => import('../views/Car.vue'),
    
  },
  
    
  
  //  {
  //    path: '/about',
  //    name: 'About',
      
  //    component: () => import('../views/About.vue')
  //  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
