const state = {
    carrinho:[]
}
const getters={
    getCarrinho:state => state.carrinho
}
const mutations={
    setCarrinho:(state,carrinho)=>(state.carrinho.push(carrinho))    
}
const actions ={ 
    insertCarrinho({commit},x){
        commit('setCarrinho',x);
    }
}


export default {
    state,
    mutations,
    actions,
    getters
}